:root {
	--layout-spacing-0: 0px;
	--layout-spacing-1: 4px;
	--layout-spacing-2: 8px;
	--layout-spacing-3: 16px;
	--layout-spacing-4: 24px;
	--layout-spacing-5: 32px;
	--layout-spacing-6: 40px;
	--layout-spacing-7: 48px;
	--layout-spacing-8: 64px;
	--layout-spacing-9: 96px;
	--layout-spacing-10: 112px;
	--layout-spacing-11: 128px;
	--layout-border-radius-0: 0px;
	--layout-border-radius-1: 4px;
	--layout-border-radius-2: 6px;
	--layout-border-radius-3: 8px;
	--layout-border-radius-4: 50%;
	--typography-font-family-body: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
	--typography-font-family-mono: ui-monospace, SFMono-Regular, SF Mono, Menlo, Consolas, Liberation Mono, monospace;
	--typography-font-size-0: 12px;
	--typography-font-size-1: 14px;
	--typography-font-size-2: 16px;
	--typography-font-size-3: 20px;
	--typography-font-size-4: 24px;
	--typography-font-size-5: 32px;
	--typography-font-size-6: 40px;
	--typography-font-size-7: 48px;
	--typography-line-height-condensed-ultra: 1;
	--typography-line-height-condensed: 1.25;
	--typography-line-height-default: 1.5;
	--typography-font-weight-thin: 100;
	--typography-font-weight-extra-light: 200;
	--typography-font-weight-light: 300;
	--typography-font-weight-regular: 400;
	--typography-font-weight-medium: 500;
	--typography-font-weight-semi-bold: 600;
	--typography-font-weight-bold: 700;
	--typography-font-weight-extra-bold: 800;
	--typography-font-weight-black: 900;
	--typography-font-weight-extra-black: 950;
}
